.bodyPage div,
.bodyPage span,
.bodyPage tr,
.bodyPage td,
.bodyPage th,
.bodyPage p,
.bodyPage h2,
.bodyPage h1 {
  /* H3 Desktop 18 pt */

  font-family: "GothamRnd-Book2";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  height: 100%;
}
.bodyPage h2,
.bodyPage h1 {
  font-size: 22px;
}
.questionContent {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  overflow-wrap: anywhere;
}
.questionName {
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
}
.mathzoneMultipleChoiceFlexBox {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  width: 90%;
  margin-top: 1rem;
  cursor: pointer;
}
.mathzoneMultipleChoiceFlexBox > div {
  min-width: calc(50% - 0.5rem);
  max-width: 100%;
  flex: 1;
  display: flex;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  align-items: center;
  padding-left: 18px;
  color: #233584;
  border-radius: 12px;
  word-break: break;
  min-height: auto;
  height: 60px;
  gap: 2rem;
  border: 1px solid black;
  height: auto;
}

.mathzoneSelectedChoiceType {
  background-color: #b9c2fc;
}

.mathzone-circle-selectbox {
  min-width: 40px;
  aspect-ratio: 1/1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 50%;
  border: 1.5px solid lightgray;
}

.mathzoneMainOlBox {
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
  width: 100%;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  gap: 2rem;
  border-radius: 5px;
}

.mathzoneMainOlBox > div {
  max-width: 90%;
  width: fit-content;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  border-radius: 5px;
  justify-content: center;
  background-color: #5ed0f3;
  color: white;
  font: 20px;
}

.mathzoneHorizontalolBox {
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.mathzoneHorizontalolBox > div {
  padding: 10px;
  border-radius: 5px;
  background-color: #5ed0f3;
  color: white;
}
.a4Page {
  max-width: 100%;
  margin: auto auto; /* Center the page horizontally */
  /* Optional: Add a border for visualization */
  padding: 20px; /* Optional: Add padding for content */
  overflow-wrap: anywhere;
}

.next-button {
  background-color: #4caf50; /* Green background */
  border: none;
  color: white;
  padding: 12px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.next-button:hover {
  background-color: #45a049; /* Darker green on hover */
}

.prev-button {
  background-color: #f44336; /* Red background */
  border: none;
  color: white;
  padding: 12px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.prev-button:hover {
  background-color: #d32f2f; /* Darker red on hover */
}
.reading_comprehensive_btn {
  position: absolute;
  top: calc(50% - 68px);
  background: initial;
  border: 0;
  cursor: pointer;
  z-index: 2;
}
.reading_comprehensive_btn img {
  transition: filter 0.3s;
}
.reading_comprehensive_btn img:hover {
  filter: brightness(50%);
}
.reading_comprehensive_btn.reading_comprehensive_btn.prev_btn {
  left: 0;
}
.reading_comprehensive_btn.next_btn {
  right: 0px;
}

.gpt_feedback_box {
  margin-top: 10px;
  border: 1px solid gainsboro;
  max-width: 80%;
}
.checkButton {
  position: static;
  z-index: 9;
}

.checkButtonColor {
  background-color: blue;
  color: white;
  width: 100px;
  height: 40px;
  border-radius: 20px;
  cursor: pointer;
}

.naviagationButton {
  opacity: 1;
  font-size: 100px;
  margin: 0px !important;
}
.naviagationButton .fa {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}
.naviagationButton .fa-caret-left:before {
  content: "\f0d9";
}

.naviagationButton .fa-caret-right:before {
  content: "\f0da";
}
.match_the_following_DropContainer {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}
.match_the_following_Drop_Inner_Container {
  width: auto;
  margin: 1rem 0;
  display: flex;
  gap: 2rem;
  flex-direction: column;
}
.match_the_following_Drop_Inner_Container > div {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.match_the_following_dragContainer {
  display: flex;
  margin-top: 2rem;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}
.match_the_following_dragContainer > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.match_the_following_drag_inner_container {
  min-height: 50px;
  height: auto;
  width: auto;
  text-align: center;
  min-width: 96px;
  border: 1px dashed black;
  padding: 8px;
}

.match_the_following_drag_inner_container > div {
  min-width: inherit;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
}
